export default {
    state:{
        show:false
    },
    getters:{
        not_show(state){//statestate
            return !state.show;
        }
    },
    mutations:{
        changeTwoState(state){ // statestate
            state.show = true;
            console.log('store show change');
        }
    },
    actions:{
        changeTwoState(context){ // context$store
            context.commit('changeTwoState');
            //mutations
        },
    }
}